@media screen and ( min-width : 990px ) { 
    header {
        display:inline-block;
        background-color: rgb(0, 0, 0);
        width: 100%;
        opacity: 0.8;
        position: fixed;
        z-index: 995;
    }
    
    .navbar ul {
        display: inline;
    }
    
    .navbar li {
        list-style: none;
        float:left;
        line-height:4.77rem;
        color: #ccc;
        margin-left: 2.5rem;
    }
    
    .navbar li:hover {
        text-shadow: 0 0 10px #69e0ff, 0 0 20px #69e0ff, 0 0 40px #69e0ff;
        color: #fff;
    }
    
    .logo {
        margin-top: 1.1rem;
        float:left;
        margin-left: 6rem;
        margin-right: 10rem;
    }
    
    .login-btn {
        padding-top: 1.4rem;
        float: right;
        display: flex;
        align-items: center;
        color: #ccc;
        background: transparent;
        border: 0px;
        width: 10rem;
        cursor: pointer;
        transition: 0.3s;
    }
    
    .login-btn > span {
        padding-right: 0.5rem;
    }
    
    .login-btn > img {
        filter: contrast(50%);
    }
    
    .login-btn:hover {
        color: #fff;
    }
    
    .login-btn:hover > img {
        filter: brightness(200%);
    }
    
    .download-btn {
        float: right;
        border: 0;
        padding: 0;
        cursor: pointer;
        height: 1px;
    }

    .toggle-btn {
        display: none;
    }

    .toggle-menu {
        display: none;
    }
}

@media screen and ( max-width : 989px ) {
    header {
        display:inline-block;
        background-color: rgb(0, 0, 0);
        width: 100%;
        opacity: 0.8;
        position: fixed;
        text-align: center;
        z-index: 995;
    }

    .logo {
        margin-top: 1.1rem;
    }

    header > div {
        display: none;
    }

    .navbar ul {
        padding: 0;
        height: 5rem;
    }

    .navbar li {
        display: none;
    }

    .download-btn {
        display: none;
    }

    .login-btn {
        display: none;
    }

    .toggle-btn {
        position: absolute;
        top: 1rem;
        right: 2rem;
        bottom: 1rem;
        background-color: rgb(0, 0, 0);
        border: 1px solid white;
        border-radius: 5px;
        line-height: 0px;
        cursor: pointer;
    }

    .toggle-btn > img {
        width: 2rem;
    }

    .toggle-menu {
        position: absolute;
        background-color: rgb(0, 0, 0);
        opacity: 0.9;
        margin: 0;
        padding: 0;
        width: 100%;
    }

    .toggle-menu li {
        padding-top: 1rem;
        padding-bottom: 1rem;
        list-style: none;
        color: white;
    }
    
    .Link-textdecoration {
        text-decoration: none;
    }
}