.mySwiper {
    width: 70%;
}

.swiper-slide img {
    width: 70%;
    margin-left: 15%;
  }
  .swiper-button-next {
    background: center/cover no-repeat url(../images/swiper_nav_right.png);
  }

  .swiper-button-prev {
    background: center/cover no-repeat url(../images/swiper_nav_left.png);
  }

  .swiper-button-next, .swiper-button-prev {
    --swiper-navigation-size:80px;
    width: calc(var(--swiper-navigation-size)/ 44 * 37);
  }

  .swiper-button-next:hover, .swiper-button-prev:hover {
    filter: opacity(0.2) drop-shadow(0 0 0 red);
    transition: 0.3s;
  }

  .swiper-button-prev:after, .swiper-button-next:after {
      content: none;
  }