@font-face {
    font-family: "Black Han Sans";
    src: url("../font/BlackHanSans-Regular.ttf") format("truetype");
    font-weight: normal;
  } 

.container {
    display: flex;
    flex-direction: column;
}

.main_image {
    width: 100%;
    height: 100vh;
    background: center/cover no-repeat url(../images/main.png);
}

.crossflatforms {
    position: absolute;
    transform: translate(50%);
    width: 50%;
    top: 80%;
    height: fit-content;
    text-align: center;
}

.crossflatforms-btn.play-station {
    background: center/cover no-repeat url(../images/play-station.png);
}

.crossflatforms-btn.app-store {
    background: center/cover no-repeat url(../images/app-store.png);
}

.crossflatforms-btn.google-play {
    background: center/cover no-repeat url(../images/google-play.png);
}

.crossflatforms-btn.windows {
    background: center/cover no-repeat url(../images/windows.png);
}

.crossflatforms-btn {
    display: inline-block;
    width: 188px;
    height: 62px;
    margin-right: 10px;
    opacity: 0.7;
    cursor: pointer;
    transition: 0.3s;
}

.crossflatforms-btn:hover {
    opacity: 1;
}

.trailer {
    position: absolute;
    width: 100%;
    height: 70%;
    text-align: center;
}

.trailer-text {
    transform: translate(0%, 30%);
    height: 50%;
    background: center/contain no-repeat url(../images/trailer_text.png);
}

.trailer-modal, .trailer-modal-mobile {
    transform: translate(0%, 250%);
    width: 5rem;
    height: 5rem;
    background: center/contain no-repeat url(../images/play_btn.png);
    cursor: pointer;
    transition: 0.3s;
}

.trailer-modal:hover {
    filter: opacity(0.2) drop-shadow(0 0 0 red);
}

.trailer-youtube {
    position: fixed;
    transform: translate(0%, -50%);
    width: 100%;
    z-index: 997;
}

.trailer-youtube-close-btn {
    display: inline-block;
    width: 32px;
    height: 32px;
    margin-left: 10px;
    cursor: pointer;
    vertical-align: top;
    background: center/contain no-repeat url(../images/trailer_close_btn.png);
}

.trailer-youtube-close-btn:hover {
    filter: opacity(0.2) drop-shadow(0 0 0 red);
    transition: 0.3s;
}

.trailer-mask {
    position: absolute;
    content: "";
    width: 100vw;
    height: 200vh;
    background-color: rgba(0, 0, 0, 0.5);
    top: 0;
    left: 0;
}

@media screen and ( min-width : 990px ) {

    .trailer-modal {
        display: inline-block;
    }

    .trailer-modal-mobile {
        display: none;
    }
}

@media screen and ( max-width : 989px ) {

    .trailer-modal {
        display: none;
    }

    .trailer-modal-mobile {
        display: inline-block;
    }
}

@media screen and ( max-width : 809px ) {

    .trailer-text {
        transform: translate(0%, 20%);
    }

    .trailer-modal-mobile {
        transform: translate(0%, 100%);
    }

    .crossflatforms {
        top: 60%
    }

    .my-swiper-wrapper {
        display: none;
    }

    .game_feature {
        display: none;
    }
}

.main_to_swiper {
    position: absolute;
    top: 100%;
    width: 100%;
    height: 8px;
    background: center/cover no-repeat url(../images/main_to_swiper.png);
}

.my-swiper-wrapper {
    width: 100%;
    height: 100%;
    background-color: white;
    padding-top: 15%;
    padding-bottom: 5%;
    background: center/cover no-repeat url(../images/main_2.jpg);
}

.game_feature {
    font-family: 'Black Han Sans', sans-serif;
    font-size: 50px;
    color: #DD4D3B;
    text-align: center;
    transform: translate(0%, 100%);
}