.footer {
    width: 100%;
    height: fit-content;
    background-color: black;
    color: white;
    text-align: center;
}

.footer > img {
    margin: 2rem 2rem;
}

.text-decoration {
    text-decoration: none;
    margin-right: 1rem;
    color: #999999;
    transition: 0.3s;
}

.text-decoration:hover {
    color: white;
}

.footer-text-links {
    margin-bottom: 2rem;
    font-size: 12px;
}

.game-info-img > img {
    margin-left: 1rem;
    margin-right: 1rem;
    margin-bottom: 2rem;
}

.footer-texts {
    color: #999999;
    font-size: 12px;
}

.copyright {
    padding: 1rem 1rem;
}