@font-face {
    font-family: "NanumPenScript-Regular";
    src: url("../font/NanumPenScript-Regular.ttf") format("truetype");
    font-weight: normal;
  } 

.character_container {
	display: flex;
    flex-direction: column;
}

.character_main_img {
    width: 100%;
    height: 100vh;
    background: center/cover no-repeat url(../images/character_main.jpg);
}

.character_table {
	font-family: 'NanumPenScript-Regular', sans-serif;
	font-size: 25px;
	margin-top: 10%;
	width: 50%;
	text-align: center;
	margin-left: 25%;
	border-collapse: collapse;
	border-radius: 10px;
	border-style: hidden;
	box-shadow: 0 0 0 3px black;
	background-color: rgba(224, 224, 209);
}

.character_table thead tr > th {
	border: 1px solid black;
	padding: 10px;
}

.character_table tbody tr > td {
	border: 1px solid black;
	vertical-align: middle;
}

.profile-picture {
	width: 0px;
}

.pagination {
	margin-top: 5%;
	text-align: center;
}

.page-btn-select {
	background-color: black;
	color: white;
	margin: 5px;
	border-radius: 100%;
	cursor: pointer;
}

.page-btn {
	margin: 5px;
	border-radius: 100%;
	cursor: pointer;
}

@media screen and ( max-width : 871px ) {
	.character_container {
		display: none;
	}
}
